'use client';

import { Award, Users, Target, Clock } from 'lucide-react';

export default function AboutSection() {
  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const features = [
    {
      icon: Award,
      title: 'Certificação Profissional',
      description: 'Personal trainers certificados com anos de experiência e formação especializada.'
    },
    {
      icon: Users,
      title: 'Acompanhamento Personalizado',
      description: 'Treinos adaptados ao seu perfil, objetivos e limitações individuais.'
    },
    {
      icon: Target,
      title: 'Resultados Comprovados',
      description: 'Metodologia testada com mais de 500 transformações bem-sucedidas.'
    },
    {
      icon: Clock,
      title: 'Flexibilidade de Horários',
      description: 'Horários flexíveis que se adaptam à sua rotina e disponibilidade.'
    }
  ];

  return (
    <section id="sobre" className="py-20 bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-16">
          <h2 className="text-3xl md:text-4xl font-bold text-primary-black mb-4">
            Por que escolher o Ricardo Team?
          </h2>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Somos especialistas em transformação corporal com uma abordagem personalizada 
            e resultados comprovados.
          </p>
        </div>

        {/* Features Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 mb-16">
          {features.map((feature, index) => (
            <div key={index} className="bg-white rounded-lg p-6 text-center hover:shadow-lg transition-shadow duration-200">
              <div className="w-16 h-16 bg-primary-red rounded-lg flex items-center justify-center mx-auto mb-4">
                <feature.icon className="w-8 h-8 text-white" />
              </div>
              <h3 className="text-lg font-semibold text-primary-black mb-2">
                {feature.title}
              </h3>
              <p className="text-gray-600 text-sm leading-relaxed">
                {feature.description}
              </p>
            </div>
          ))}
        </div>

        {/* About Content */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
          {/* Text Content */}
          <div>
            <h3 className="text-2xl font-bold text-primary-black mb-6">
              Transformação que vai além do físico
            </h3>
            <div className="space-y-4 text-gray-600">
              <p>
                No Ricardo Team, acreditamos que a verdadeira transformação acontece quando 
                corpo e mente trabalham em harmonia. Nossa metodologia vai além dos exercícios 
                físicos, incluindo orientação nutricional e suporte motivacional.
              </p>
              <p>
                Com mais de 5 anos de experiência, nossa equipe já ajudou centenas de pessoas 
                a alcançarem seus objetivos, seja emagrecimento, ganho de massa muscular ou 
                melhoria da qualidade de vida.
              </p>
            </div>
            
            <button
              onClick={() => scrollToSection('planos')}
              className="mt-8 bg-accent-yellow text-primary-black px-8 py-3 rounded-lg font-semibold hover:bg-yellow-500 transition-colors duration-200"
            >
              Começar Minha Transformação
            </button>
          </div>

          {/* Stats */}
          <div className="bg-white rounded-lg p-8">
            <h4 className="text-xl font-bold text-primary-black mb-6 text-center">
              Nossos Números
            </h4>
            <div className="grid grid-cols-2 gap-6">
              <div className="text-center">
                <div className="text-3xl font-bold text-primary-red mb-1">500+</div>
                <div className="text-sm text-gray-600">Clientes Atendidos</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-primary-red mb-1">98%</div>
                <div className="text-sm text-gray-600">Taxa de Satisfação</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-primary-red mb-1">5+</div>
                <div className="text-sm text-gray-600">Anos de Experiência</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-primary-red mb-1">24/7</div>
                <div className="text-sm text-gray-600">Suporte Disponível</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}