'use client';

import { Check, Crown, Zap } from 'lucide-react';
import { trainingPlans } from '@/data/mockData';

export default function PlansSection() {
  const formatPrice = (priceInCents: number) => {
    return (priceInCents / 100).toLocaleString('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    });
  };

  const handleSelectPlan = (planId: string) => {
    // Scroll para a seção de pagamento
    const paymentSection = document.getElementById('payment');
    paymentSection?.scrollIntoView({ behavior: 'smooth' });
    
    // Aqui você pode adicionar lógica para selecionar o plano
    // Por exemplo, salvar no localStorage ou estado global
    localStorage.setItem('selectedPlan', planId);
  };

  return (
    <section id="plans" className="py-20 bg-white">
      <div className="container mx-auto px-4">
        {/* Cabeçalho da seção */}
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-inter font-bold text-primary-black mb-6">
            Escolha Seu
            <span className="gradient-text"> Plano</span>
          </h2>
          <p className="text-lg text-gray-600 max-w-3xl mx-auto leading-relaxed">
            Planos flexíveis para todos os perfis e objetivos. Comece sua transformação 
            hoje mesmo com o acompanhamento profissional do Ricardo Team.
          </p>
        </div>

        {/* Grid de planos */}
        <div className="grid md:grid-cols-3 gap-8 max-w-6xl mx-auto">
          {trainingPlans.map((plan, index) => (
            <div
              key={plan.id}
              className={`relative rounded-3xl p-8 transition-all duration-300 ${
                plan.highlighted
                  ? 'bg-gradient-orange text-white scale-105 shadow-2xl'
                  : 'bg-secondary-gray text-primary-black hover:shadow-xl'
              }`}
            >
              {/* Badge para plano destacado */}
              {plan.highlighted && (
                <div className="absolute -top-4 left-1/2 transform -translate-x-1/2">
                  <div className="bg-white text-primary-orange px-6 py-2 rounded-full text-sm font-bold flex items-center gap-2">
                    <Crown className="w-4 h-4" />
                    MAIS POPULAR
                  </div>
                </div>
              )}

              {/* Ícone do plano */}
              <div className={`w-16 h-16 rounded-2xl flex items-center justify-center mb-6 ${
                plan.highlighted ? 'bg-white/20' : 'bg-gradient-orange'
              }`}>
                <Zap className={`w-8 h-8 ${plan.highlighted ? 'text-white' : 'text-white'}`} />
              </div>

              {/* Nome do plano */}
              <h3 className="text-2xl font-inter font-bold mb-2">
                {plan.name}
              </h3>

              {/* Duração */}
              <p className={`mb-6 ${plan.highlighted ? 'text-white/80' : 'text-gray-600'}`}>
                {plan.duration}
              </p>

              {/* Preço */}
              <div className="mb-8">
                <div className="flex items-baseline gap-2">
                  <span className="text-4xl font-bold">
                    {formatPrice(plan.price)}
                  </span>
                  <span className={`text-sm ${plan.highlighted ? 'text-white/80' : 'text-gray-500'}`}>
                    /{plan.duration}
                  </span>
                </div>
                {plan.highlighted && (
                  <p className="text-white/80 text-sm mt-2">
                    Melhor custo-benefício
                  </p>
                )}
              </div>

              {/* Lista de recursos */}
              <ul className="space-y-4 mb-8">
                {plan.features.map((feature, featureIndex) => (
                  <li key={featureIndex} className="flex items-start gap-3">
                    <div className={`w-5 h-5 rounded-full flex items-center justify-center mt-0.5 flex-shrink-0 ${
                      plan.highlighted ? 'bg-white/20' : 'bg-primary-orange'
                    }`}>
                      <Check className={`w-3 h-3 ${plan.highlighted ? 'text-white' : 'text-white'}`} />
                    </div>
                    <span className={`${plan.highlighted ? 'text-white' : 'text-gray-700'}`}>
                      {feature}
                    </span>
                  </li>
                ))}
              </ul>

              {/* Botão de seleção */}
              <button
                onClick={() => handleSelectPlan(plan.id)}
                className={`w-full py-4 rounded-xl font-semibold text-lg transition-all duration-300 ${
                  plan.highlighted
                    ? 'bg-white text-primary-orange hover:bg-gray-100'
                    : 'bg-gradient-orange text-white hover:shadow-lg hover:shadow-orange-500/25'
                }`}
              >
                {plan.highlighted ? 'Escolher Premium' : 'Selecionar Plano'}
              </button>

              {/* Garantia */}
              {plan.highlighted && (
                <p className="text-center text-white/80 text-sm mt-4">
                  ✨ Garantia de 30 dias ou seu dinheiro de volta
                </p>
              )}
            </div>
          ))}
        </div>

        {/* Informações adicionais */}
        <div className="mt-16 text-center">
          <div className="bg-secondary-gray rounded-2xl p-8 max-w-4xl mx-auto">
            <h3 className="text-2xl font-inter font-bold text-primary-black mb-4">
              Todos os planos incluem:
            </h3>
            <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6 text-left">
              <div className="flex items-start gap-3">
                <Check className="w-5 h-5 text-primary-orange mt-1 flex-shrink-0" />
                <span className="text-gray-700">Avaliação física completa</span>
              </div>
              <div className="flex items-start gap-3">
                <Check className="w-5 h-5 text-primary-orange mt-1 flex-shrink-0" />
                <span className="text-gray-700">Treino 100% personalizado</span>
              </div>
              <div className="flex items-start gap-3">
                <Check className="w-5 h-5 text-primary-orange mt-1 flex-shrink-0" />
                <span className="text-gray-700">Acompanhamento profissional</span>
              </div>
              <div className="flex items-start gap-3">
                <Check className="w-5 h-5 text-primary-orange mt-1 flex-shrink-0" />
                <span className="text-gray-700">Suporte via WhatsApp</span>
              </div>
            </div>
          </div>
        </div>

        {/* Call to action para dúvidas */}
        <div className="text-center mt-12">
          <p className="text-gray-600 mb-4">
            Ainda tem dúvidas sobre qual plano escolher?
          </p>
          <button 
            onClick={() => {
              const whatsappUrl = `https://wa.me/5511999999999?text=${encodeURIComponent('Olá! Tenho dúvidas sobre os planos do Ricardo Team. Podem me ajudar?')}`;
              window.open(whatsappUrl, '_blank');
            }}
            className="border-2 border-primary-orange text-primary-orange hover:bg-primary-orange hover:text-white px-8 py-3 rounded-lg font-semibold transition-all duration-300"
          >
            Falar com Especialista
          </button>
        </div>
      </div>
    </section>
  );
}