'use client';

import { useState } from 'react';
import { Menu, X, Dumbbell } from 'lucide-react';

export default function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
      setIsMenuOpen(false);
    }
  };

  return (
    <header className="fixed top-0 left-0 right-0 z-50 bg-white/95 backdrop-blur-sm border-b border-gray-100">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16">
          {/* Logo */}
          <div className="flex items-center space-x-3">
            <div className="w-10 h-10 bg-primary-red rounded-lg flex items-center justify-center">
              <Dumbbell className="w-6 h-6 text-white" />
            </div>
            <span className="text-xl font-bold text-primary-black">Ricardo Team</span>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-8">
            <button
              onClick={() => scrollToSection('inicio')}
              className="text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium"
            >
              Início
            </button>
            <button
              onClick={() => scrollToSection('sobre')}
              className="text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium"
            >
              Sobre
            </button>
            <button
              onClick={() => scrollToSection('treinos')}
              className="text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium"
            >
              Treinos
            </button>
            <button
              onClick={() => scrollToSection('depoimentos')}
              className="text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium"
            >
              Depoimentos
            </button>
            <button
              onClick={() => scrollToSection('planos')}
              className="text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium"
            >
              Planos
            </button>
          </nav>

          {/* CTA Button */}
          <div className="hidden md:block">
            <button
              onClick={() => scrollToSection('planos')}
              className="bg-accent-yellow text-primary-black px-6 py-2 rounded-lg font-semibold hover:bg-yellow-500 transition-colors duration-200"
            >
              Começar Agora
            </button>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="md:hidden p-2 rounded-lg hover:bg-gray-100 transition-colors duration-200"
          >
            {isMenuOpen ? (
              <X className="w-6 h-6 text-gray-600" />
            ) : (
              <Menu className="w-6 h-6 text-gray-600" />
            )}
          </button>
        </div>

        {/* Mobile Menu */}
        {isMenuOpen && (
          <div className="md:hidden py-4 border-t border-gray-100">
            <nav className="flex flex-col space-y-4">
              <button
                onClick={() => scrollToSection('inicio')}
                className="text-left text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium py-2"
              >
                Início
              </button>
              <button
                onClick={() => scrollToSection('sobre')}
                className="text-left text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium py-2"
              >
                Sobre
              </button>
              <button
                onClick={() => scrollToSection('treinos')}
                className="text-left text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium py-2"
              >
                Treinos
              </button>
              <button
                onClick={() => scrollToSection('depoimentos')}
                className="text-left text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium py-2"
              >
                Depoimentos
              </button>
              <button
                onClick={() => scrollToSection('planos')}
                className="text-left text-gray-600 hover:text-primary-red transition-colors duration-200 font-medium py-2"
              >
                Planos
              </button>
              <button
                onClick={() => scrollToSection('planos')}
                className="bg-accent-yellow text-primary-black px-6 py-3 rounded-lg font-semibold hover:bg-yellow-500 transition-colors duration-200 mt-4"
              >
                Começar Agora
              </button>
            </nav>
          </div>
        )}
      </div>
    </header>
  );
}