'use client';

import { MessageCircle, X } from 'lucide-react';
import { useState } from 'react';
import { whatsappConfig } from '@/data/mockData';

export default function WhatsAppFloat() {
  const [isExpanded, setIsExpanded] = useState(false);

  const openWhatsApp = (message?: string) => {
    const finalMessage = message || whatsappConfig.defaultMessage;
    const whatsappUrl = `https://wa.me/${whatsappConfig.phoneNumber}?text=${encodeURIComponent(finalMessage)}`;
    window.open(whatsappUrl, '_blank');
    setIsExpanded(false);
  };

  const quickMessages = [
    'Quero saber mais sobre os treinos',
    'Tenho dúvidas sobre os planos',
    'Gostaria de agendar uma avaliação',
    'Preciso de ajuda para escolher o treino ideal'
  ];

  return (
    <>
      {/* Overlay quando expandido */}
      {isExpanded && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 z-40"
          onClick={() => setIsExpanded(false)}
        />
      )}

      {/* Widget expandido */}
      {isExpanded && (
        <div className="fixed bottom-24 right-4 bg-white rounded-2xl shadow-2xl z-50 w-80 max-w-[calc(100vw-2rem)]">
          {/* Cabeçalho */}
          <div className="bg-green-500 text-white p-4 rounded-t-2xl flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center">
                <MessageCircle className="w-6 h-6 text-green-500" />
              </div>
              <div>
                <h4 className="font-semibold">Ricardo Team</h4>
                <p className="text-sm opacity-90">Online agora</p>
              </div>
            </div>
            <button
              onClick={() => setIsExpanded(false)}
              className="text-white hover:bg-white/20 p-1 rounded-full transition-colors"
            >
              <X className="w-5 h-5" />
            </button>
          </div>

          {/* Conteúdo */}
          <div className="p-4">
            <p className="text-gray-700 mb-4">
              Olá! 👋 Como podemos ajudar você hoje?
            </p>

            {/* Mensagens rápidas */}
            <div className="space-y-2">
              {quickMessages.map((message, index) => (
                <button
                  key={index}
                  onClick={() => openWhatsApp(message)}
                  className="w-full text-left p-3 bg-gray-50 hover:bg-gray-100 rounded-lg text-sm transition-colors"
                >
                  {message}
                </button>
              ))}
            </div>

            {/* Botão principal */}
            <button
              onClick={() => openWhatsApp()}
              className="w-full bg-green-500 text-white py-3 rounded-lg font-semibold mt-4 hover:bg-green-600 transition-colors flex items-center justify-center gap-2"
            >
              <MessageCircle className="w-5 h-5" />
              Iniciar Conversa
            </button>

            {/* Informações de contato */}
            <div className="text-center mt-3 text-xs text-gray-500">
              Respondemos em até 5 minutos
            </div>
          </div>
        </div>
      )}

      {/* Botão flutuante principal */}
      <button
        onClick={() => setIsExpanded(!isExpanded)}
        className="whatsapp-float bg-green-500 hover:bg-green-600 text-white w-16 h-16 rounded-full shadow-lg flex items-center justify-center transition-all duration-300 z-50"
        aria-label="Abrir WhatsApp"
      >
        {isExpanded ? (
          <X className="w-8 h-8" />
        ) : (
          <MessageCircle className="w-8 h-8" />
        )}
      </button>

      {/* Tooltip */}
      {!isExpanded && (
        <div className="fixed bottom-20 right-20 bg-primary-black text-white px-3 py-2 rounded-lg text-sm opacity-0 hover:opacity-100 transition-opacity duration-300 pointer-events-none z-40">
          Fale conosco no WhatsApp
          <div className="absolute top-full left-1/2 transform -translate-x-1/2 border-4 border-transparent border-t-primary-black"></div>
        </div>
      )}
    </>
  );
}