'use client';

import { useState, useEffect } from 'react';
import { CreditCard, Shield, Lock, CheckCircle } from 'lucide-react';
import { trainingPlans } from '@/data/mockData';
import { PaymentData } from '@/types';

export default function PaymentSection() {
  const [selectedPlan, setSelectedPlan] = useState<string>('');
  const [formData, setFormData] = useState<PaymentData>({
    planId: '',
    customerName: '',
    customerEmail: '',
    customerPhone: '',
    amount: 0
  });
  const [isLoading, setIsLoading] = useState(false);

  // Verificar se há um plano selecionado no localStorage
  useEffect(() => {
    const savedPlan = localStorage.getItem('selectedPlan');
    if (savedPlan) {
      setSelectedPlan(savedPlan);
      const plan = trainingPlans.find(p => p.id === savedPlan);
      if (plan) {
        setFormData(prev => ({
          ...prev,
          planId: savedPlan,
          amount: plan.price
        }));
      }
    }
  }, []);

  const selectedPlanData = trainingPlans.find(plan => plan.id === selectedPlan);

  const formatPrice = (priceInCents: number) => {
    return (priceInCents / 100).toLocaleString('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    });
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handlePlanSelect = (planId: string) => {
    const plan = trainingPlans.find(p => p.id === planId);
    if (plan) {
      setSelectedPlan(planId);
      setFormData(prev => ({
        ...prev,
        planId,
        amount: plan.price
      }));
      localStorage.setItem('selectedPlan', planId);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      // Simulação da integração com Infinity Pay
      // Em produção, você faria uma chamada para sua API backend
      const paymentData = {
        amount: formData.amount,
        currency: 'BRL',
        customer: {
          name: formData.customerName,
          email: formData.customerEmail,
          phone: formData.customerPhone
        },
        description: `Plano ${selectedPlanData?.name} - Ricardo Team`
      };

      // Simular delay da API
      await new Promise(resolve => setTimeout(resolve, 2000));

      // Em produção, você redirecionaria para a URL de pagamento retornada pela API
      const paymentUrl = `https://pay.infinitypay.io/checkout?amount=${paymentData.amount}&customer=${encodeURIComponent(paymentData.customer.name)}`;
      
      // Redirecionar para o pagamento
      window.open(paymentUrl, '_blank');
      
      // Limpar formulário
      setFormData({
        planId: '',
        customerName: '',
        customerEmail: '',
        customerPhone: '',
        amount: 0
      });
      setSelectedPlan('');
      localStorage.removeItem('selectedPlan');

    } catch (error) {
      console.error('Erro ao processar pagamento:', error);
      alert('Erro ao processar pagamento. Tente novamente.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <section id="payment" className="py-20 bg-secondary-gray">
      <div className="container mx-auto px-4">
        <div className="max-w-4xl mx-auto">
          {/* Cabeçalho da seção */}
          <div className="text-center mb-12">
            <h2 className="text-4xl md:text-5xl font-inter font-bold text-primary-black mb-6">
              Finalizar
              <span className="gradient-text"> Pagamento</span>
            </h2>
            <p className="text-lg text-gray-600 leading-relaxed">
              Pagamento 100% seguro processado pela Infinity Pay. 
              Comece sua transformação hoje mesmo!
            </p>
          </div>

          <div className="grid lg:grid-cols-2 gap-12">
            {/* Seleção de plano */}
            <div className="space-y-6">
              <h3 className="text-2xl font-inter font-bold text-primary-black mb-6">
                1. Escolha seu plano
              </h3>
              
              <div className="space-y-4">
                {trainingPlans.map((plan) => (
                  <div
                    key={plan.id}
                    className={`border-2 rounded-xl p-6 cursor-pointer transition-all duration-300 ${
                      selectedPlan === plan.id
                        ? 'border-primary-orange bg-orange-50'
                        : 'border-gray-200 hover:border-primary-orange'
                    }`}
                    onClick={() => handlePlanSelect(plan.id)}
                  >
                    <div className="flex items-center justify-between">
                      <div>
                        <h4 className="font-bold text-lg text-primary-black">
                          {plan.name}
                        </h4>
                        <p className="text-gray-600">{plan.duration}</p>
                      </div>
                      <div className="text-right">
                        <div className="text-2xl font-bold text-primary-orange">
                          {formatPrice(plan.price)}
                        </div>
                        {plan.highlighted && (
                          <span className="text-sm text-primary-orange font-semibold">
                            Mais Popular
                          </span>
                        )}
                      </div>
                    </div>
                    
                    {selectedPlan === plan.id && (
                      <div className="mt-4 pt-4 border-t border-orange-200">
                        <div className="flex items-center gap-2 text-primary-orange">
                          <CheckCircle className="w-5 h-5" />
                          <span className="font-semibold">Plano selecionado</span>
                        </div>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            </div>

            {/* Formulário de pagamento */}
            <div className="bg-white rounded-2xl p-8 shadow-lg">
              <h3 className="text-2xl font-inter font-bold text-primary-black mb-6">
                2. Dados para pagamento
              </h3>

              <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                  <label className="block text-sm font-semibold text-gray-700 mb-2">
                    Nome completo
                  </label>
                  <input
                    type="text"
                    name="customerName"
                    value={formData.customerName}
                    onChange={handleInputChange}
                    required
                    className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-orange focus:border-transparent"
                    placeholder="Seu nome completo"
                  />
                </div>

                <div>
                  <label className="block text-sm font-semibold text-gray-700 mb-2">
                    E-mail
                  </label>
                  <input
                    type="email"
                    name="customerEmail"
                    value={formData.customerEmail}
                    onChange={handleInputChange}
                    required
                    className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-orange focus:border-transparent"
                    placeholder="seu@email.com"
                  />
                </div>

                <div>
                  <label className="block text-sm font-semibold text-gray-700 mb-2">
                    WhatsApp
                  </label>
                  <input
                    type="tel"
                    name="customerPhone"
                    value={formData.customerPhone}
                    onChange={handleInputChange}
                    required
                    className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-orange focus:border-transparent"
                    placeholder="(11) 99999-9999"
                  />
                </div>

                {/* Resumo do pedido */}
                {selectedPlanData && (
                  <div className="bg-secondary-gray rounded-lg p-6">
                    <h4 className="font-semibold text-primary-black mb-4">Resumo do pedido:</h4>
                    <div className="flex justify-between items-center mb-2">
                      <span>{selectedPlanData.name}</span>
                      <span className="font-semibold">{formatPrice(selectedPlanData.price)}</span>
                    </div>
                    <div className="flex justify-between items-center text-lg font-bold text-primary-orange border-t pt-2">
                      <span>Total:</span>
                      <span>{formatPrice(selectedPlanData.price)}</span>
                    </div>
                  </div>
                )}

                {/* Botão de pagamento */}
                <button
                  type="submit"
                  disabled={!selectedPlan || isLoading}
                  className="w-full bg-gradient-orange text-white py-4 rounded-lg font-semibold text-lg hover:shadow-lg transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
                >
                  {isLoading ? (
                    <>
                      <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                      Processando...
                    </>
                  ) : (
                    <>
                      <CreditCard className="w-5 h-5" />
                      Pagar com Infinity Pay
                    </>
                  )}
                </button>

                {/* Informações de segurança */}
                <div className="flex items-center justify-center gap-6 text-sm text-gray-600 pt-4">
                  <div className="flex items-center gap-2">
                    <Shield className="w-4 h-4 text-green-600" />
                    <span>Pagamento Seguro</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <Lock className="w-4 h-4 text-green-600" />
                    <span>SSL Criptografado</span>
                  </div>
                </div>
              </form>
            </div>
          </div>

          {/* Garantias e informações */}
          <div className="mt-12 text-center">
            <div className="bg-white rounded-2xl p-8 shadow-lg">
              <h4 className="text-xl font-inter font-bold text-primary-black mb-4">
                Suas garantias:
              </h4>
              <div className="grid md:grid-cols-3 gap-6">
                <div className="text-center">
                  <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-3">
                    <Shield className="w-6 h-6 text-green-600" />
                  </div>
                  <h5 className="font-semibold text-primary-black mb-2">Pagamento Seguro</h5>
                  <p className="text-sm text-gray-600">Processado pela Infinity Pay com máxima segurança</p>
                </div>
                <div className="text-center">
                  <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-3">
                    <CheckCircle className="w-6 h-6 text-blue-600" />
                  </div>
                  <h5 className="font-semibold text-primary-black mb-2">Garantia de 30 dias</h5>
                  <p className="text-sm text-gray-600">Não ficou satisfeito? Devolvemos seu dinheiro</p>
                </div>
                <div className="text-center">
                  <div className="w-12 h-12 bg-orange-100 rounded-full flex items-center justify-center mx-auto mb-3">
                    <CreditCard className="w-6 h-6 text-primary-orange" />
                  </div>
                  <h5 className="font-semibold text-primary-black mb-2">Parcelamento</h5>
                  <p className="text-sm text-gray-600">Parcele em até 12x sem juros no cartão</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}