'use client';

import { Dumbbell, MapPin, Phone, Mail, Instagram, Facebook, Youtube } from 'lucide-react';

export default function Footer() {
  const currentYear = new Date().getFullYear();

  const scrollToSection = (sectionId: string) => {
    const section = document.getElementById(sectionId);
    section?.scrollIntoView({ behavior: 'smooth' });
  };

  const openWhatsApp = () => {
    const whatsappUrl = `https://wa.me/5511999999999?text=${encodeURIComponent('Olá! Gostaria de saber mais sobre os treinos do Ricardo Team.')}`;
    window.open(whatsappUrl, '_blank');
  };

  return (
    <footer id="contact" className="bg-primary-black text-white">
      {/* Seção principal do footer */}
      <div className="container mx-auto px-4 py-16">
        <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
          {/* Informações da empresa */}
          <div className="space-y-6">
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-gradient-red rounded-lg flex items-center justify-center iron-glow">
                <Dumbbell className="w-7 h-7 text-white" />
              </div>
              <div>
                <h3 className="text-xl font-inter font-bold">Ricardo Team</h3>
                <p className="text-secondary-gray-medium text-sm">Personal Trainers</p>
              </div>
            </div>
            <p className="text-secondary-gray-light leading-relaxed">
              Transformando vidas através do fitness com treinos personalizados 
              e acompanhamento profissional. Sua jornada de transformação começa aqui.
            </p>
            <div className="flex space-x-4">
              <a 
                href="#" 
                className="w-10 h-10 bg-secondary-gray-dark/30 rounded-full flex items-center justify-center hover:bg-primary-red hover:scale-110 transition-all duration-300"
                aria-label="Instagram"
              >
                <Instagram className="w-5 h-5" />
              </a>
              <a 
                href="#" 
                className="w-10 h-10 bg-secondary-gray-dark/30 rounded-full flex items-center justify-center hover:bg-primary-red hover:scale-110 transition-all duration-300"
                aria-label="Facebook"
              >
                <Facebook className="w-5 h-5" />
              </a>
              <a 
                href="#" 
                className="w-10 h-10 bg-secondary-gray-dark/30 rounded-full flex items-center justify-center hover:bg-primary-red hover:scale-110 transition-all duration-300"
                aria-label="YouTube"
              >
                <Youtube className="w-5 h-5" />
              </a>
            </div>
          </div>

          {/* Links rápidos */}
          <div>
            <h4 className="text-lg font-inter font-semibold mb-6">Links Rápidos</h4>
            <ul className="space-y-3">
              <li>
                <button 
                  onClick={() => scrollToSection('about')}
                  className="text-secondary-light hover:text-primary-red transition-colors duration-200"
                >
                  Sobre Nós
                </button>
              </li>
              <li>
                <button 
                  onClick={() => scrollToSection('training-types')}
                  className="text-secondary-light hover:text-primary-red transition-colors duration-200"
                >
                  Tipos de Treinos
                </button>
              </li>
              <li>
                <button 
                  onClick={() => scrollToSection('testimonials')}
                  className="text-secondary-light hover:text-primary-red transition-colors duration-200"
                >
                  Depoimentos
                </button>
              </li>
              <li>
                <button 
                  onClick={() => scrollToSection('plans')}
                  className="text-secondary-light hover:text-primary-red transition-colors duration-200"
                >
                  Planos e Preços
                </button>
              </li>
            </ul>
          </div>

          {/* Serviços */}
          <div>
            <h4 className="text-lg font-inter font-semibold mb-6">Nossos Serviços</h4>
            <ul className="space-y-3 text-secondary-light">
              <li>Personal Training</li>
              <li>Treino para Emagrecimento</li>
              <li>Treino de Força</li>
              <li>Musculação</li>
              <li>Treino Funcional</li>
              <li>Consultoria Nutricional</li>
            </ul>
          </div>

          {/* Contato */}
          <div>
            <h4 className="text-lg font-inter font-semibold mb-6">Contato</h4>
            <div className="space-y-4">
              <div className="flex items-start gap-3">
                <MapPin className="w-5 h-5 text-primary-red mt-1 flex-shrink-0" />
                <div className="text-secondary-light">
                  <p>Rua das Academias, 123</p>
                  <p>São Paulo - SP, 01234-567</p>
                </div>
              </div>
              <div className="flex items-center gap-3">
                <Phone className="w-5 h-5 text-primary-red flex-shrink-0" />
                <a 
                  href="tel:+5511999999999" 
                  className="text-secondary-light hover:text-primary-red transition-colors duration-200"
                >
                  (11) 99999-9999
                </a>
              </div>
              <div className="flex items-center gap-3">
                <Mail className="w-5 h-5 text-primary-red flex-shrink-0" />
                <a 
                  href="mailto:contato@ricardoteam.com" 
                  className="text-secondary-light hover:text-primary-red transition-colors duration-200"
                >
                  contato@ricardoteam.com
                </a>
              </div>
            </div>

            {/* Botão WhatsApp */}
            <button
              onClick={openWhatsApp}
              className="mt-6 bg-green-500 hover:bg-green-600 text-white px-6 py-3 rounded-lg font-semibold transition-colors flex items-center gap-2"
            >
              <Phone className="w-5 h-5" />
              Falar no WhatsApp
            </button>
          </div>
        </div>
      </div>

      {/* Seção de horários */}
      <div className="border-t border-secondary-gray-dark">
        <div className="container mx-auto px-4 py-8">
          <div className="text-center">
            <h4 className="text-lg font-inter font-semibold mb-4">Horários de Atendimento</h4>
            <div className="grid md:grid-cols-2 gap-4 max-w-2xl mx-auto text-secondary-gray-light">
              <div>
                <p className="font-semibold">Segunda a Sexta</p>
                <p>06:00 - 22:00</p>
              </div>
              <div>
                <p className="font-semibold">Sábados e Domingos</p>
                <p>08:00 - 18:00</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Copyright */}
      <div className="border-t border-secondary-gray-dark">
        <div className="container mx-auto px-4 py-6">
          <div className="flex flex-col md:flex-row justify-between items-center gap-4">
            <p className="text-secondary-gray-medium text-sm">
              © {currentYear} Ricardo Team. Todos os direitos reservados.
            </p>
            <div className="flex gap-6 text-sm text-secondary-gray-medium">
              <a href="#" className="hover:text-primary-orange transition-colors duration-200">
                Política de Privacidade
              </a>
              <a href="#" className="hover:text-primary-orange transition-colors duration-200">
                Termos de Uso
              </a>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}