'use client';

import { Dumbbell, Heart, Zap, Target } from 'lucide-react';

export default function TrainingTypesSection() {
  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const trainingTypes = [
    {
      icon: Dumbbell,
      title: 'Musculação',
      description: 'Treinos focados em hipertrofia e ganho de força muscular.',
      features: ['Treino personalizado', 'Acompanhamento técnico', 'Progressão controlada']
    },
    {
      icon: Heart,
      title: 'Emagrecimento',
      description: 'Programas específicos para perda de peso e definição corporal.',
      features: ['Queima de gordura', 'Orientação nutricional', 'Resultados rápidos']
    },
    {
      icon: Zap,
      title: 'Condicionamento',
      description: 'Melhore sua resistência e capacidade cardiovascular.',
      features: ['Exercícios funcionais', 'Melhora do fôlego', 'Mais disposição']
    },
    {
      icon: Target,
      title: 'Reabilitação',
      description: 'Exercícios terapêuticos para recuperação e prevenção.',
      features: ['Exercícios seguros', 'Recuperação guiada', 'Prevenção de lesões']
    }
  ];

  return (
    <section id="treinos" className="py-20 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-16">
          <h2 className="text-3xl md:text-4xl font-bold text-primary-black mb-4">
            Tipos de Treino
          </h2>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Oferecemos diferentes modalidades de treino para atender seus objetivos específicos.
          </p>
        </div>

        {/* Training Types Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 mb-16">
          {trainingTypes.map((type, index) => (
            <div key={index} className="bg-gray-50 rounded-lg p-6 hover:shadow-lg transition-shadow duration-200">
              <div className="w-16 h-16 bg-primary-red rounded-lg flex items-center justify-center mb-4">
                <type.icon className="w-8 h-8 text-white" />
              </div>
              
              <h3 className="text-xl font-semibold text-primary-black mb-3">
                {type.title}
              </h3>
              
              <p className="text-gray-600 mb-4 text-sm leading-relaxed">
                {type.description}
              </p>
              
              <ul className="space-y-2">
                {type.features.map((feature, featureIndex) => (
                  <li key={featureIndex} className="flex items-center text-sm text-gray-600">
                    <div className="w-1.5 h-1.5 bg-primary-red rounded-full mr-2"></div>
                    {feature}
                  </li>
                ))}
              </ul>
            </div>
          ))}
        </div>

        {/* CTA Section */}
        <div className="bg-gray-50 rounded-lg p-8 text-center">
          <h3 className="text-2xl font-bold text-primary-black mb-4">
            Não sabe qual treino é ideal para você?
          </h3>
          <p className="text-gray-600 mb-6 max-w-2xl mx-auto">
            Nossa equipe fará uma avaliação completa para criar o programa de treino 
            perfeito para seus objetivos e condicionamento atual.
          </p>
          
          <div className="flex flex-col sm:flex-row items-center justify-center space-y-4 sm:space-y-0 sm:space-x-4">
            <button
              onClick={() => scrollToSection('planos')}
              className="bg-accent-yellow text-primary-black px-8 py-3 rounded-lg font-semibold hover:bg-yellow-500 transition-colors duration-200"
            >
              Ver Planos
            </button>
            
            <button
              onClick={() => scrollToSection('depoimentos')}
              className="border-2 border-gray-300 text-gray-700 px-8 py-3 rounded-lg font-semibold hover:border-primary-red hover:text-primary-red transition-colors duration-200"
            >
              Ver Depoimentos
            </button>
          </div>
        </div>
      </div>
    </section>
  );
}