'use client';

import { ArrowRight, Play, Star } from 'lucide-react';

export default function HeroSection() {
  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <section id="inicio" className="pt-24 pb-16 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center max-w-4xl mx-auto">
          {/* Badge */}
          <div className="inline-flex items-center space-x-2 bg-gray-50 rounded-full px-4 py-2 mb-8">
            <Star className="w-4 h-4 text-accent-yellow fill-current" />
            <span className="text-sm font-medium text-gray-700">Personal Trainer Certificado</span>
          </div>

          {/* Main Heading */}
          <h1 className="text-4xl md:text-6xl font-bold text-primary-black mb-6 leading-tight">
            Transforme seu corpo,
            <br />
            <span className="text-primary-red">mude sua vida</span>
          </h1>

          {/* Subtitle */}
          <p className="text-xl text-gray-600 mb-12 max-w-2xl mx-auto leading-relaxed">
            Treinamento personalizado com resultados comprovados. 
            Mais de 500 pessoas já transformaram suas vidas conosco.
          </p>

          {/* CTA Buttons */}
          <div className="flex flex-col sm:flex-row items-center justify-center space-y-4 sm:space-y-0 sm:space-x-6 mb-16">
            <button
              onClick={() => scrollToSection('planos')}
              className="bg-accent-yellow text-primary-black px-8 py-4 rounded-lg font-semibold text-lg hover:bg-yellow-500 transition-colors duration-200 flex items-center space-x-2"
            >
              <span>Começar Agora</span>
              <ArrowRight className="w-5 h-5" />
            </button>
            
            <button
              onClick={() => scrollToSection('sobre')}
              className="border-2 border-gray-300 text-gray-700 px-8 py-4 rounded-lg font-semibold text-lg hover:border-primary-red hover:text-primary-red transition-colors duration-200 flex items-center space-x-2"
            >
              <Play className="w-5 h-5" />
              <span>Saiba Mais</span>
            </button>
          </div>

          {/* Stats */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-3xl mx-auto">
            <div className="text-center">
              <div className="text-3xl font-bold text-primary-red mb-2">500+</div>
              <div className="text-gray-600">Clientes Transformados</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-primary-red mb-2">98%</div>
              <div className="text-gray-600">Taxa de Satisfação</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-primary-red mb-2">5+</div>
              <div className="text-gray-600">Anos de Experiência</div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}