'use client';

import { useState } from 'react';
import { ChevronLeft, ChevronRight, Quote, Star } from 'lucide-react';
import { testimonials } from '@/data/mockData';

export default function TestimonialsSection() {
  const [currentIndex, setCurrentIndex] = useState(0);

  const nextTestimonial = () => {
    setCurrentIndex((prev) => (prev + 1) % testimonials.length);
  };

  const prevTestimonial = () => {
    setCurrentIndex((prev) => (prev - 1 + testimonials.length) % testimonials.length);
  };

  const goToTestimonial = (index: number) => {
    setCurrentIndex(index);
  };

  return (
    <section id="testimonials" className="py-20 bg-primary-black text-white relative overflow-hidden">
      {/* Background decorativo */}
      <div className="absolute inset-0 opacity-10">
        <div className="absolute top-10 left-10 w-32 h-32 border border-primary-red rounded-full"></div>
            <div className="absolute bottom-10 right-10 w-48 h-48 border border-primary-red rounded-full"></div>
            <div className="absolute top-1/2 left-1/4 w-16 h-16 bg-primary-red rounded-full"></div>
      </div>

      <div className="container mx-auto px-4 relative z-10">
        {/* Cabeçalho da seção */}
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-inter font-bold mb-6">
            O Que Nossos
            <span className="gradient-text"> Clientes Dizem</span>
          </h2>
          <p className="text-lg text-gray-300 max-w-3xl mx-auto leading-relaxed">
            Histórias reais de transformação. Veja os resultados que nossos clientes 
            alcançaram com o acompanhamento do Ricardo Team.
          </p>
        </div>

        {/* Carousel de depoimentos */}
        <div className="max-w-4xl mx-auto">
          <div className="relative">
            {/* Depoimento atual */}
            <div className="bg-white/10 backdrop-blur-sm rounded-3xl p-8 md:p-12 text-center">
              {/* Ícone de aspas */}
              <div className="w-16 h-16 bg-gradient-red rounded-full flex items-center justify-center mx-auto mb-8 iron-glow">
                <Quote className="w-8 h-8 text-white" />
              </div>

              {/* Foto do cliente */}
              <div className="w-24 h-24 mx-auto mb-6 rounded-full overflow-hidden border-4 border-primary-red">
                <img
                  src={testimonials[currentIndex].photo}
                  alt={testimonials[currentIndex].name}
                  className="w-full h-full object-cover"
                />
              </div>

              {/* Avaliação com estrelas */}
              <div className="flex justify-center mb-6">
                {[...Array(5)].map((_, i) => (
                  <Star key={i} className="w-5 h-5 text-yellow-400 fill-current" />
                ))}
              </div>

              {/* Texto do depoimento */}
              <blockquote className="text-xl md:text-2xl font-medium mb-6 leading-relaxed italic">
                &ldquo;{testimonials[currentIndex].text}&rdquo;
              </blockquote>

              {/* Nome e resultado */}
              <div className="space-y-2">
                <h4 className="text-xl font-inter font-bold text-primary-red">
                  {testimonials[currentIndex].name}
                </h4>
                <p className="text-gray-300 font-semibold">
                  {testimonials[currentIndex].result}
                </p>
              </div>
            </div>

            {/* Botões de navegação */}
            <button
              onClick={prevTestimonial}
              className="absolute left-4 top-1/2 transform -translate-y-1/2 w-12 h-12 bg-primary-red rounded-full flex items-center justify-center hover:bg-red-600 transition-colors duration-300"
            >
              <ChevronLeft className="w-6 h-6 text-white" />
            </button>

            <button
              onClick={nextTestimonial}
              className="absolute right-4 top-1/2 transform -translate-y-1/2 w-12 h-12 bg-primary-red rounded-full flex items-center justify-center hover:bg-red-600 transition-colors duration-300"
            >
              <ChevronRight className="w-6 h-6 text-white" />
            </button>
          </div>

          {/* Indicadores de navegação */}
          <div className="flex justify-center mt-8 space-x-3">
            {testimonials.map((_, index) => (
              <button
                key={index}
                onClick={() => goToTestimonial(index)}
                className={`w-3 h-3 rounded-full transition-all duration-300 ${
                  index === currentIndex
                    ? 'bg-primary-red scale-125'
                    : 'bg-white/30 hover:bg-white/50'
                }`}
              />
            ))}
          </div>
        </div>

        {/* Estatísticas de satisfação */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mt-16 max-w-4xl mx-auto">
          <div className="text-center">
            <div className="text-4xl font-bold text-primary-red mb-2">98%</div>
            <div className="text-gray-300">Taxa de Satisfação</div>
          </div>
          <div className="text-center">
            <div className="text-4xl font-bold text-primary-red mb-2">500+</div>
            <div className="text-gray-300">Clientes Transformados</div>
          </div>
          <div className="text-center">
            <div className="text-4xl font-bold text-primary-red mb-2">5.0</div>
            <div className="text-gray-300">Avaliação Média</div>
          </div>
        </div>

        {/* Call to action */}
        <div className="text-center mt-12">
          <p className="text-lg text-gray-300 mb-6">
            Pronto para ser nossa próxima história de sucesso?
          </p>
          <button 
            onClick={() => {
              const plansSection = document.getElementById('plans');
              plansSection?.scrollIntoView({ behavior: 'smooth' });
            }}
            className="bg-gradient-yellow text-primary-black px-8 py-4 rounded-lg font-semibold text-lg hover:shadow-lg transition-all duration-300 gym-shadow-yellow"
          >
            Começar Minha Transformação
          </button>
        </div>
      </div>
    </section>
  );
}